.company-section {
    position: relative;
    text-align: center;
    background: var(--white);
    padding: 50px 20px;
    font-family: var(--primary-font);
    /* background: radial-gradient(circle, rgba(255, 255, 255, 1) 60%, rgba(105, 101, 213, 0.8) 85%, rgba(3, 19, 251, 0.5) 100%); */
}

.company-section {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 1s ease-out, transform 1s ease-out;
}

.company-section.visible {
    opacity: 1;
    transform: translateY(0);
}

.company-content {
    z-index: 1;
}

.company-content h1 {
    font-size: 60px;
}

.company-logos img {
    margin: 0 10px;
    height: 40px;
    opacity: 0.8;
}


/* Logo Animations */

.company-logos img {
    margin: 0 10px;
    height: 40px;
    opacity: 0.8;
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.company-logos img:hover {
    transform: scale(1.1) rotate(5deg);
    opacity: 1;
}

.company-stats {
    margin-top: 20px;
}

.company-stats h2 {
    font-size: 34px;
    font-weight: bold;
}

.company-stats p {
    font-size: 16px;
    color: gray;
}

.cta-button {
    margin-top: 20px;
    background-color: #e63946;
    color: var(--white);
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
}

.npackage {
    font-weight: 600;
    background-color: var(--primary-color);
    width: 20%;
    color: var(--white);
    border: none;
    padding: 10px 19px;
    border-radius: 0.5em;
    font-family: var(--primary-font);
    margin-top: 10px;
}

.cta-button:hover {
    background-color: #d62839;
}


/* Triangle Separator */

.ctriangle-separator {
    position: absolute;
    left: 0;
    width: 100%;
    height: 50px;
    /* Adjust thickness of the triangle */
    background: #e9ebf7;
    /* Light purple color */
}

.ctriangle-top {
    top: 0;
    clip-path: polygon(0 0, 100% 0, 100% 100%);
}

.ctriangle-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50px;
    background: #e9ebf7;
    /* Light purple color */
    clip-path: polygon(0 100%, 100% 0, 100% 100%);
}

@media (max-width: 797px) {
    .npackage {
        width: 45%;
    }
}


/* Button Animation */

.npackage {
    font-weight: 600;
    background-color: var(--primary-color);
    width: 20%;
    color: var(--white);
    border: none;
    padding: 10px 19px;
    border-radius: 0.5em;
    font-family: var(--primary-font);
    margin-top: 10px;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.npackage:hover {
    background-color: #0056b3;
    transform: translateY(-3px);
}


/* Company Stats Movement */

.company-stats {
    margin-top: 20px;
    animation: fadeInUp 1.2s ease;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}


/* Triangle Separator (No changes here) */

